<script>
  import firebase from 'firebase'
  import axios from "axios"
  import tmi from "tmi.js"

  export default {
    data() {
      return {
        channelName: this.$_getchannel(),
        username: this.$_getusername(),
        password: this.$_getpassword(),
        channelURL: ``,
        email: this.$_getuseremail(),
        ChatCommand: '',
        sendTimer: true,
        timeoutTimer: '',
        buttonChangeTimer: true,
        buttonChanger: 1,
        value: 0,
        l_w: true, l_a: true, l_s: true, l_d: true,
        l_vw: true, l_va: true, l_vs: true, l_vd: true,
        disabled: true
      }
    },
    created() {
      //外す必要あり
      //this.channelURL = `https://player.twitch.tv/?channel=${this.channelName}&parent=it0sat0.github.io`
      //console.log("channelURL:"+this.channelURL)
    },
    mounted: function(){
      // 初期化処理
      //外す必要あり
      //var obj=document.getElementById("ifr")
      //obj.removeAttribute("src")
      //document.getElementById("ifr").src = this.channelURL
      this.timeout()
    },
    methods: { 
      gotoBefore(){
        this.$router.push('/about')
      },
      gotoUI1(){
        this.$router.push('/ui1')
      },
      gotoUI2(){
        this.$router.push('/ui2')
      },
      crossKeyCommand(command){
        clearTimeout(this.timeoutTimer)
        if(this.buttonChangeTimer){
          //if(command !='cr' && command !='op' && command !='cl'){
            //this.buttonChanger++
          //}
          //for(this.i =0; this.i<this.buttonChanger+1; this.i++){
              this.post(command+","+ (parseInt(this.buttonChanger)+1))
              //setTimeout(500)
            //}
              console.log("post")
              this.buttonChangeTimer=false
          /*
          setTimeout(() => {
            for(this.i =0; this.i<this.buttonChanger+1; this.i++){
              this.post(command)
              setTimeout(300)
            }
              console.log("post")
              this.buttonChangeTimer=false
            }
            ,1000 //時間変更可能
          )
          */
          if(this.sendTimer){
            this.sendTimer = false
            setTimeout(() => {
                this.sendTimer = true
                this.buttonChangeTimer=true
                //this.buttonChanger=0
              }
              //,3000 //時間変更可能
              ,4000 //もともとは4000
            )
          }
        }
        this.timeout()
      },
      buttonCommand(command){
        clearTimeout(this.timeoutTimer)
        if(this.buttonChangeTimer){
          this.post(command)
          console.log("post")
          this.buttonChangeTimer=false
          if(this.sendTimer){
            this.sendTimer = false
            setTimeout(() => {
                this.sendTimer = true
                this.buttonChangeTimer=true
              }
              ,4000 //もともとは4000
            )
          }
        }
        this.timeout()
      },
      func() {
       console.log(this.channelURL)
      },
      getxy: function(e){
        clearTimeout(this.timeoutTimer)
        this.buttonChangeTimer=false
        console.log("sendTimer"+this.sendTimer)
        if(this.sendTimer){
          this.post("mouse,"+(e.offsetX/930)+","+(e.offsetY/567))
          //this.post("m,"+(e.offsetX/930)+","+(e.offsetY/510.3))
          console.log("e.offsetX"+ e.offsetX/930)
          console.log("e.offsetY"+ e.offsetY/567)
          //console.log("e.offsetY"+ e.offsetY/510.3)
          
          this.sendTimer = false
        }
        setTimeout(() => {
          this.sendTimer = true
          this.buttonChangeTimer=true
          }
          ,3000 //時間変更可能 3000
        )
        this.timeout()
      },
      timeout: function(){
        this.timeoutTimer = setTimeout(() => {
          alert("一定時間操作がなかったため、ログアウトします")
          clearTimeout(this.timeoutTimer)
          this.logout()
          }
          ,600000 //時間変更可能 600000
        )
      },
      check: function(){
        //外す必要あり
        //console.log(this.channelURL)
        //var obj=document.getElementById("ifr")
        //obj.removeAttribute("src")
        //document.getElementById("ifr").src = this.channelURL
      },
      logout: function(){
        clearTimeout(this.timeoutTimer)
        this.logouotRequest(this.email)
        firebase.auth().signOut();
        clearTimeout(this.timeoutTimer)
        this.$router.push('/')
      },
      logouotRequest: async function(username){
        const params = new URLSearchParams();
        params.append("name",username);
        await axios.post("http://localhost:8040/logout", params)
        //await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/logout", params)
        alert("ログアウトしました")
      },
      post(command){
        console.log("OK")
        /*
        const opts = {
          channels: [this.channelName],
          identity: {
            username: this.username,
            password: this.password 
          }
        };
        */
       /*
        const opts = {
          channels: ["it0sat0"],
          identity: {
            username: "it0sat0sub",
            password: "oauth:3rl5k8t3wz5q4brrex0alg0efbr1h2"
          }
        };
        */
        const opts = {
          channels: ["it0sat0sub2"],
          identity: {
            username: "it0sat0sub3",
            password: "oauth:w0bcb3fazr9bne37u8gtlhl8mdbcc4"
          }
        };

        // Create a client with our options
        const client = new tmi.client(opts);

        client.connect();

        // Register our event handlers (defined below)
        client.on('connected', onConnectedHandler);

        function onConnectedHandler (addr, port) {
          console.log(`* Connected to ${addr}:${port}`);
          client.say('it0sat0', `${command}`);
        }
      },
    }
  }
</script>

